import {
  Link
} from 'react-router-dom';
import logo from '../../../assets/image/logo.png';

import { useState, useEffect } from 'react';
import styles from './customers-page.module.css';
import Logout from '../../shared/components/logout';
import { useAppDispatch, useAppSelector } from '../../config/hooks';
import { AuthenticationState, selectAuthentication } from '../../authentication/presentation/authentication-slice';
import { getAllCustomers, GetAllCustomersState, selectGetAllCustomers } from './get-all-customers-slice';
import UserModel from '../../authentication/core/domain/user.model';

export default function CustomersPage() {
  const [currentCustomers, setCurrentCustomers] = useState<Array<UserModel>>([]);
  const [sortedCustomers, setSortedCustomers] = useState<Array<UserModel>>([]);
  const [sort, setSort] = useState('name');
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(true);
  const [searchValue, setSearchValue] = useState('');

  const dispatch = useAppDispatch();
  const authenticationState = useAppSelector(selectAuthentication);
  const getAllCustomersState = useAppSelector(selectGetAllCustomers);

  useEffect(()=>{
    if(authenticationState.status === AuthenticationState.success){
      if(authenticationState.token)
        dispatch(getAllCustomers({token: authenticationState.token}));
    }
  },[authenticationState,dispatch]);

  useEffect(()=>{
    switch(getAllCustomersState.status){
      case GetAllCustomersState.success:
        
        setCurrentCustomers(getAllCustomersState.data);
        break
    }
  }, [getAllCustomersState]);

  const sortFunction = (type : string ) => {
    setSort(type)
    if(type === 'name'){
      currentCustomers.sort((a : UserModel, b: UserModel) => `${a.firstName} ${a.middleName} ${a.lastName}` > `${b.firstName} ${b.middleName} ${b.lastName}` ? 1 : `${b.firstName} ${b.middleName} ${b.lastName}` > `${a.firstName} ${a.middleName} ${a.lastName}` ? -1 : 0)
    }else if(type === 'address'){
      currentCustomers.sort((a : UserModel, b: UserModel) => `${a.address.street} ${a.address.city} ${a.address.state} ${a.address.zipcode}` > `${b.address.street} ${b.address.city} ${b.address.state} ${b.address.zipcode}` ? 1 : `${b.address.street} ${b.address.city} ${b.address.state} ${b.address.zipcode}` > `${a.address.street} ${a.address.city} ${a.address.state} ${a.address.zipcode}` ? -1 : 0)
    }else{
      currentCustomers.sort((a: any, b: any) => a[type] > b[type] ? 1 : b[type] > a[type] ? -1 : 0)
    }
  }

  const back = () =>{
    if(page > 1){
      setCurrentCustomers(()=>{
        return sortedCustomers.slice((page-2)* 7, (page-1) *7)
      })
      setPage(prev => prev-1);
      setSearchValue('');
    }
  }

  const forward = () =>{
    if(searchValue === ''){
      if(page < Math.ceil(getAllCustomersState.data.length / 7)){
        setCurrentCustomers(()=>{
          return sortedCustomers.slice((page)* 7, (page+1) *7)
        })
        setPage(prev => prev+1);
        setSearchValue('');
      }
    }else{
      if(page < Math.ceil(currentCustomers.length / 7)){
        setCurrentCustomers(()=>{
          return sortedCustomers.slice((page)* 7, (page+1) *7)
        })
        setPage(prev => prev+1);
        setSearchValue('');
      }
    }
  }

  const search = (value : string) =>{
    setSearchValue(value);
    setPage(1)
    setCurrentCustomers(() => {
      const tempCustomers = sortedCustomers.filter((customer : UserModel) => {

        if (customer.firstName.toLowerCase().includes(value, 0) || customer.middleName.toLowerCase().includes(value, 0) || customer.lastName.toLowerCase().includes(value, 0) || customer._id.toLowerCase().includes(value, 0) || customer.address.street.toLowerCase().includes(value, 0) || customer.address.city.toLowerCase().includes(value, 0) || customer.address.state.toLowerCase().includes(value, 0) || customer.address.zipcode.toLowerCase().includes(value, 0) || customer.email.toLowerCase().includes(value, 0) || customer.contactNumber.toLowerCase().includes(value, 0)) {
          return customer;
        }else{
          return null
        }
      });
      return tempCustomers;
    });
  }

    return (
      <div className={styles.container}>
        <div className={styles.leftNavigation}>
          <div className={styles.imageContainer}>
            <img alt="logo" src={logo} className={styles.image}/>
          </div>
          <Link to="/orders">
            <div className={styles.tabContainer}>
                Orders
            </div>
          </Link>
          <Link to="/products">
            <div className={styles.tabContainer}>
                Products
            </div>
          </Link>
          <Link to="/customers">
            <div className={`${styles.tabContainer} ${styles.active}`}>
                Customers
            </div>
          </Link>
          <Logout />
        </div>
        <div className={styles.contentContainer}>
  
          <div className={styles.headingContainer}>
            <div className={styles.titleContainer}>
              Customers
            </div>
  
            <div className={styles.buttonContainer}>
  
              <input className={styles.search} placeholder='🔎︎ Search' value={searchValue} onChange={(event)=> search(event.target.value.toLocaleLowerCase())}/>
  
  
              <div className={styles.deleteContainer}>
                Delete
              </div>
            </div>
          </div>
  
          <div className={styles.gridContainerHeading}>
              <div className={styles.item}></div>
              <div className={styles.itemHeader} onClick={()=>{sortFunction('name')}}>Name  {sort === 'name' ? <span>▼</span> : <span>►</span>}</div>
              <div className={styles.itemHeader} onClick={()=>{sortFunction('userID')}}>User ID  {sort === 'userID' ? <span>▼</span> : <span>►</span>}</div>
              <div className={styles.itemHeader} onClick={()=>{sortFunction('address')}}>Address {sort === 'address' ? <span>▼</span> : <span>►</span>}</div>
              <div className={styles.itemHeader} onClick={()=>{sortFunction('email')}}>Email {sort === 'email' ? <span>▼</span> : <span>►</span>}</div>
              <div className={styles.itemHeader} onClick={()=>{sortFunction('contactNumber')}}>Contact Number  {sort === 'contactNumber' ? <span>▼</span> : <span>►</span>}</div>
          </div>
        
          {
            currentCustomers.map((customer : UserModel, index)=>{
              if(index < 7){
                return(
                  <div className={styles.gridContainer} key={index}>
                    <div className={styles.item}><input type="checkbox" id={customer._id} name={customer._id} value={customer._id} /></div>
                    <div className={styles.item}>{`${customer.firstName} ${customer.middleName} ${customer.lastName}`}</div>
                    <div className={styles.item}>{customer._id}</div>
                    <div className={styles.item}>{`${customer.address.street} ${customer.address.city} ${customer.address.state} ${customer.address.zipcode}`}</div>
                    <div className={styles.item}>{customer.email}</div>
                    <div className={styles.item}>{customer.contactNumber}</div>
                  </div>
                )
              }else{
                return null
              }
            })
          }

          <div className={styles.arrowContainer}>
            <div className={styles.arrowLeft} onClick={back}>◄</div>
            {
              searchValue === '' ?
              <p>{page} of {Math.ceil(sortedCustomers.length / 7)}</p>:
              <p>{page} of {Math.ceil(currentCustomers.length / 7)}</p>
            }
            <div className={styles.arrowRight}  onClick={forward}>►</div>
          </div>
          
        </div>
      </div>
    );
}
